.section{
    width: 100vw;
    min-height: 100vh;
    position: relative;
    background-color: black;
    overflow: hidden;
}
.v1{
    position: absolute;
    object-fit: cover;
    object-position: bottom;
}
.v2{
    position: absolute;
    bottom: 0;
    right: 0;
    object-fit: cover;
    object-position: bottom;
}
.titleContainer {
    width: 50%;
    height: 50%;
    display: flex;
    flex-direction: column;
    position: absolute;
    right: -50vw;
    top: 12vh;
}
.span{
    font-size: 50px;
    text-transform: capitalize;
    animation-fill-mode: forwards;
}
@keyframes fadeInAnimation {
    0% {
        transform: translateX(0) scale(1);
        background-image: linear-gradient(-45deg, #35c3f3 0%, #8b9fe8 20%, #e681d8 39%, #ffa9a4 76%, #fed2ce 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;

    }
    100% {
        transform: translateX(-20vw) scale(1.6);
        background-image: linear-gradient(90deg, #35c3f3 0%, #8b9fe8 20%, #e681d8 39%, #ffa9a4 76%, #fed2ce 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;

    }
}
@keyframes fadeInAnimation2 {
    0% {
        transform: translate(-100%,-100%) scale(3) ;
        width: 100%;
        height: 100vh;
        z-index: 2;


    }
    65%{
        z-index: 1;
    }
    100% {
        transform: translateX(0) ;
        width: 50%;
        height: 50vh;
        z-index: 0;
    }
}
@keyframes fadeInAnimation3 {
    0% {
        transform: translate(100%,100%) scale(3) ;
        width: 0;
        height: 0;

    }
    100% {
        transform: translateX(0) ;
        width: 50%;
        height: 50vh;
    }
}
.animation2{
    animation: fadeInAnimation2 2s 1;
    animation-fill-mode: forwards;
}
.animation3{
    animation: fadeInAnimation3 2s 1;
    animation-fill-mode: forwards;
}
.animation{
    animation: fadeInAnimation 2s 1;
    animation-fill-mode: forwards;

}
.span:nth-child(1){
    animation-delay: 0.5s;
}
.span:nth-child(2){
    animation-delay: 1s;
}
.span:nth-child(3) {
    animation-delay: 1.5s;
}
@media  (min-width: 280px) and (max-width: 340px) {
    @keyframes fadeInAnimation2 {
        0% {
            transform: translate(-100%,-100%) scale(3) ;
            width: 100%;
            height: 100vh;
            z-index: 2;
        }
        65%{
            z-index: 1;
        }
        100% {
            transform: translate(27vw,-6vh) scale(0.6);
            width: 50%;
            height: 50vh;
            z-index: 0;
        }
    }
    @keyframes fadeInAnimation3 {
        0% {
            transform: translate(100%,100%) scale(3) ;
            width: 0;
            height: 0;

        }
        100% {
            transform: translate(0vw,-40vh) scale(0.7) ;
            width: 100%;
            height: 30vh;
        }
    }
    @keyframes fadeInAnimation {
        0% {
            transform: translate(-100vw,100vh) scale(1);
            background-image: linear-gradient(-45deg, #35c3f3 0%, #8b9fe8 20%, #e681d8 39%, #ffa9a4 76%, #fed2ce 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;

        }
        100% {
            transform: translate(-85vw,50vh) scale(1.4);
            background-image: linear-gradient(90deg, #35c3f3 0%, #8b9fe8 20%, #e681d8 39%, #ffa9a4 76%, #fed2ce 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            z-index: 1;

        }
    }
    .span{
        font-size: 40px;
        text-transform: capitalize;
        animation-fill-mode: forwards;
    }
}
